import React, { useEffect, useState } from "react";
import { AuthenticatedTemplate, UnauthenticatedTemplate, useMsal } from "@azure/msal-react";
import { loginRequest } from "./authConfig";
import { PageLayout } from "./components/PageLayout";
import { ProfileData } from "./components/ProfileData";
import { callMsGraph } from "./graph";
import Button from "react-bootstrap/Button";
import "./styles/App.css";
import { BrowserRouter, Route, Routes, Outlet } from "react-router-dom";
import { Login } from "./login/index";
import { Logout } from "./logout/index";

/**
 * Renders information about the signed-in user or a button to retrieve data about the user
 */
const ProfileContent = () => {
    const { instance, accounts } = useMsal();
    const [graphData, setGraphData] = useState(null);

    function RequestProfileData() {
        // Silently acquires an access token which is then attached to a request for MS Graph data
        instance
            .acquireTokenSilent({
                ...loginRequest,
                account: accounts[0],
            })
            .then((response) => {
                callMsGraph(response.accessToken).then((response) => {
                    setGraphData(response);
                    // const link = document.createElement("form");
                    // const obj = document.createElement("input");
                    // obj.setAttribute("name", "ssoAuth");
                    // obj.setAttribute("value", response);

                    // link.appendChild(obj);
                    // link.setAttribute("method", "post");
                    // link.setAttribute("action", "http://kr-dst00075.pc.cognex.com:3000/api/sso/auth");
                    // document.body.appendChild(link);
                    // link.submit();
                    const data = { givenName: response.givenName, surname: response.surname, mail: response.mail };
                    const link = document.createElement("a");
                    // link.href = `http://kr-dst00075.pc.cognex.com:3000/api/sso/auth?givenName=${response.givenName}&surname=${response.surname}&mail=${response.mail}`;
                    link.href = `http://kr-dst00075.pc.cognex.com:3000/login?givenName=${response.givenName}&surname=${response.surname}&mail=${response.mail}`;
                    document.body.appendChild(link);
                    link.click();
                });
            });
    }

    return (
        <>
            <h5 className="card-title">Welcome {accounts[0].name}</h5>
            {graphData ? (
                <ProfileData graphData={graphData} />
            ) : (
                <Button variant="secondary" onClick={RequestProfileData}>
                    Request Profile Information
                </Button>
            )}
        </>
    );
};

/**
 * If a user is authenticated the ProfileContent component above is rendered. Otherwise a message indicating a user is not authenticated is rendered.
 */

const MainContent = () => {
    return (
        <div className="App">
            <AuthenticatedTemplate>
                <ProfileContent />
            </AuthenticatedTemplate>

            <UnauthenticatedTemplate>
                <h5 className="card-title">Please sign-in to see your profile information.</h5>
            </UnauthenticatedTemplate>
        </div>
    );
};

export const DefaultApp = () => {
    return (
        <PageLayout>
            <MainContent />
            <Outlet />
        </PageLayout>
    );
};

export default function App() {
    return (
        <>
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <Routes>
                    <Route path="/" element={<DefaultApp />}>
                        <Route path="login" element={<Login />}></Route>
                        <Route path="logout" element={<Logout />}></Route>
                    </Route>
                </Routes>
            </BrowserRouter>
        </>
    );
}
